<template>
  <div class="add-tutorial">
    <router-link :to="{ name: 'addTutorial' }"
      ><el-button class="primary" plain>New Tutorial</el-button></router-link
    >
  </div>
  <el-table
    :data="
      tutorials.filter(
        (data) =>
          !search || data.title.toLowerCase().includes(search.toLowerCase())
      )
    "
    style="width: 100%"
  >
    <el-table-column label="Title" prop="title"> </el-table-column>
    <el-table-column label="Description" prop="description"> </el-table-column>
    <el-table-column label="Published" prop="published"> </el-table-column>
    <el-table-column align="right">
      <template #header>
        <el-input v-model="search" size="mini" placeholder="Type to search" />
      </template>
      <template #default="scope">
        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">
          Edit
        </el-button>

        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)"
          >Delete</el-button
        >
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    v-model:currentPage="currentPage"
    background
    :page-sizes="[5, 10, 20, 100]"
    :page-size="5"
    layout="total, sizes, prev, pager, next, jumper"
    :total="this.count"
    >>
  </el-pagination>
</template>

<script>
import TutorialDataService from "../../api/tutorial-service";

export default {
  name: "tutorials-list",
  data() {
    return {
      search: "",

      tutorials: [],
      currentTutorial: null,
      currentIndex: -1,
      searchTitle: "",
      //current page
      page: 1,
      //total pages
      count: 0,
      //number of items in each page
      pageSize: 5,

      pageSizes: [3, 6, 9],
    };
  },
  methods: {
    getRequestParams(searchTitle, page, pageSize) {
      let params = {};

      if (searchTitle) {
        params["title"] = searchTitle;
      }

      if (page) {
        console.log("PAGE: ", page);

        params["page"] = page - 1;
      }

      if (pageSize) {
        console.log("PAGE SIZE: ", pageSize);
        params["size"] = pageSize;
      }

      return params;
    },
    retrieveTutorials() {
      const params = this.getRequestParams(
        this.searchTitle,
        this.page,
        this.pageSize
      );

      TutorialDataService.getAll(params)
        .then((response) => {
          const { tutorials, totalItems } = response.data;
          this.tutorials = tutorials;
          this.count = totalItems;

          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveTutorials();
      this.currentTutorial = null;
      this.currentIndex = -1;
    },
    removeAllTutorials() {
      TutorialDataService.deleteAll()
        .then((response) => {
          console.log(response.data);
          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    handleEdit(index, row) {
      console.log(index, row);
      this.$router.push({ name: "tutorial-details", params: { id: row.id } });
    },
    handleDelete(index, row) {
      console.log(index, row);
      TutorialDataService.delete(row.id)
        .then((response) => {
          console.log(response.data);
          this.retrieveTutorials();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    handleSizeChange(val) {
      console.log(`${val} items per page`);
      this.pageSize = val;
      this.page = 1;
      this.retrieveTutorials();
    },
    handleCurrentChange(val) {
      console.log(`current page: ${val}`);
      this.page = val;
      this.retrieveTutorials();
    },
  },
  created() {
    console.log(this.retrieveTutorials());
    this.retrieveTutorials();
  },
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>